@import url("https://fonts.googleapis.com/css2?family=Aclonica&family=Montserrat:wght@100..900&display=swap");



:root{
  --header-height:3rem;



  --hue:0;
  --sat:0%;
  --title-color:hsl(var(--hue),var(--sat),50%);
  --title-color-dark:hsl(var(--hue),var(--sat), 0%);
  --text-color:hsl(var(--hue),var(--sat),46%);
  --body-color:hsl(var(--hue),var(--sat),98%);
  --container-color:#ffffff;


  --body-font:'Poppins',sans-serif;


  --big-font-size:3.5rem;
  --h1-font-size:2.25rem;
  --h2-font-size:1.5rem;
  --h3-font-size:1.25rem;
  --normal-font-size:1rem;
  --small--font-size:0.875rem;
  --smaller-font-size:0.813rem;
  --tiny-font-size:0.625rem;


  --font-normal:400;
  --font-medium:500;
  --font-semi-bold:600;



  --mb-0-25:0.25rem;
  --mb-0-5:0.5rem;
  --mb-0-75:0.7rem;
  --mb-1:1rem;
  --mb-1-5:1.5rem;
  --mb-2:2rem;
  --mb-2-5:2.5rem;
  --mb-3:3rem;


  --z-tooltip:10;
  --z-fixed:100;
  --z-modal:1000;
}


@media screen and (max-width:992px) {
  :root{
  --big-font-size:2.75rem;
  --h1-font-size:1.5rem;
  --h2-font-size:1.25rem;
  --h3-font-size:1rem;
  --normal-font-size:0.938rem;
  --small--font-size:0.813rem;
  --smaller-font-size:0.75rem;
  }
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

html{
  scroll-behavior:smooth ;
}

body,button,input,textarea{
font-family: var(--body-font);
font-size: var(--normal-font-size);
}




body{
  background-color: black;
  color: rgb(252, 251, 248);
}

h1,h2,h3{
  /* color: var(--title-color); */
  font-weight: var(--font-semi-bold);
}

ul{
  list-style:none;
}

a{
  text-decoration: none;
}

button{
  cursor: pointer;
  border: none;
  outline: none;
}

img{
  max-width: 100%;
  height: auto;
}


/* .section{
  padding:6rem 0 2rem;
} */

.section__title{
  font-size:var(--h1-font-size);
  /* color: var(--text-color); */
}

.section__subtitle{
  display: block;
  font-size: var(--small--font-size);
  margin-bottom: 4rem;
}

.section__title,
.section__subtitle{
  text-align: center;
}


.container{
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}

.grid{
  display: grid;
  gap:1.5rem;
}


.button{
  display: inline-block;
  background-color: rgb(238, 94, 16);
  color:rgb(250, 247, 247);
  padding: 1.25rem 2rem;
  border-radius: 1rem;
  font-weight: var(--font-medium);
}

/* .button:hover{
  background-color: rgb(228, 154, 44);
} */

.button__icon{
  margin-left: var(--mb-0-5);
}

.button-flex{
  display: inline-flex;
  align-items: center;
}